.checkout-img{
  height:8.5em;
  width:100%;
  margin-top:1em;

}

.map{
    height: 25em;
    width:100%;
    position: relative;}

  
    .marker{
      margin: 0;
      position: absolute;
      top:45%;
      left: 50%;
      margin-right: -50%;
      transform: translate(-50%, -50%);
      z-index:2000 
    }


  .hiddenCart{
    visibility: hidden;
    position: fixed;
    right:0;
    top:0;
    width: 0px !important;
    height: 100%;
    background:white;
    border:1px solid black;
    z-index:2002;
    transition: width 0.5s,visibility 0.25s ;
   
  }
  .hiddenCartContent{
    visibility: hidden;
    transition: visibility 0.1s;
  }
  .showCartContent{
    visibility: visible;
    transition: visibility 0.1s;
  }
  
  .showCart{
      visibility: visible;
      position: fixed;
      right:0;
      top:0;
      width: 90% !important;
      height: 100%;
      background:white;
      border:1px solid black;
      z-index:2002;
      transition: width 0.5s,visibility 0.25s ;
  
  }
   
  
